// import * as http from 'request';
import * as got from 'got';
import {BLIZZARD} from '../secrets';
import {APIGatewayEvent, Callback} from 'aws-lambda';
import {Response} from '../utils/response.util';

export class AuthHandler {
  public getAccessToken(event: APIGatewayEvent, callback: Callback): void {
    const body = JSON.parse(event.body),
      region = body.region,
      code = body.code,
      redirectURI = body.redirectURI;

    got(`https://${
      region
      }.battle.net/oauth/token`, {
      form: true,
      body: {
        code: code,
        redirect_uri: redirectURI,
        grant_type: 'authorization_code'
      },
      auth: `${BLIZZARD.CLIENT_ID}:${BLIZZARD.CLIENT_SECRET}`
    })
      .then(response =>
        Response.send(JSON.parse(response.body), callback))
      .catch(error =>
        Response.error(callback, error)
      );
  }
}
